<template>
 <div>
     <div>
         请选择产品：<da-radio-select v-model="product" :maxNum="6" :option-lists="productList" label="producName" value-key="id" @change="productChange"></da-radio-select>
         请选择产线：<da-radio-select v-model="productLine" :maxNum="7" :option-lists="productLineList" label="producName" value-key="id" @change="productLineChange"></da-radio-select>
     </div>
     <da-time-select v-model="stateTime" :show-time="[1,0]" @change="changestateTime"></da-time-select>
     <da-panel title="产线运行状态监测"  width="100%" height="auto" min-height="200px" padding="0 10px" ref="panelCount">
      <!-- productLineState -->
     </da-panel>
     <da-time-select v-model="tendencyTime" :show-time="[1,0]" @change="changeTendencyTime"></da-time-select>
     <da-panel title="产线综合能耗趋势分析"  width="100%" height="auto" min-height="200px" padding="0 10px" ref="panelCount">
      <div>
         <da-radio-block v-model="rdbSubitem" :data-lists="subitemList" label="subitemName" value-key="id" @change="subitemChange"></da-radio-block>
     </div>
     <div>
         <da-line-chart id="productLineCompare" :date-time="productLineCompareTime" :data-lists="productLineCompareData"></da-line-chart>
     </div>
     </da-panel>
     <da-panel title="产线报警日历"  width="100%" height="auto" min-height="200px" padding="0 10px" ref="panelCount">
      <!-- PLAlarmCalendar -->
     </da-panel>  
     <da-panel title="产线数据综合对比分析"  width="100%" height="auto" min-height="200px" padding="0 10px" ref="panelCount">
     <P>请选择与{{productLineNumber}}进行比对的产线：</P>
     <!-- compareTable -->
     </da-panel> 
 </div>
</template>
<script>
export default {
  data() {
    return {
      product: "",
      productList: [],

      productLine: "",
      productLineList: [],

      stateTime: "",

      productLineState: {},

      tendencyTime: "",

      rdbSubitem: "",
      subitemList: [],

      productLineCompareTime: "",
      productLineCompareData: [],

      PLAlarmCalendar: {},

      compareTable: {},

      productLineNumber: ""
    };
  },
  methods: {
    inintData() {},
    loadData() {
      this.getProductList();
      this.getProductLineList();
      this.getProductLineStateData();
      this.getProductLineCompareData();
      this.getPLAlarmCalendarData();
      this.getCompareTableData();
    },
    //获取所有产品列表
    getProductList() {},
    //选择产品
    productChange() {},
    //获得产线列表
    getProductLineList() {},
    //选择产线
    productLineChange() {},
    //改变状态时间
    changestateTime() {},
    //获得产线状态数据
    getProductLineStateData() {},
    changeTendencyTime() {},
    //改变分类
    subitemChange() {},
    //获得产线对比表格数据
    getProductLineCompareData() {},
    //获取告警日历数据
    getPLAlarmCalendarData() {},
    //获取对比表格数据
    getCompareTableData() {}
  },
  mounted() {
    this.loadData();
  }
};
</script>




